.wrapper {
  padding: 20px;
  display: flex;
  justify-content: center;
}
.character {
  display: flex;
  justify-content: center;
}
@media (max-width: 900px) {
  .character {
    display: inline-block;
    flex-direction: colum;
  }
}

.largePicture {
  width: 500px;
  padding: 10px;
}

@media (max-width: 500px) {
  .largePicture {
    width: 300px;
    height: 450px;
  }
}
.smallPicture {
  width: 300px;
  height: 450px;
  padding: 10px;
}
.desc {
  background-color: #dbdff5;
  padding: 10px;
  height: 96%;
}

.movies {
  background-color: #dbdff5;
}
.title {
  font-family: 'Title';
  font-weight: 700;
  color: #ff0000;
}

.tag {
  background: #eee;
  border-radius: 3px 0 0 3px;
  color: #999;
  display: inline-block;
  height: 26px;
  line-height: 26px;
  padding: 0 20px 0 23px;
  position: relative;
  margin: 0 10px 10px 0;
  text-decoration: none;
  -webkit-transition: color 0.2s;
}

.tag::before {
  background: #fff;
  border-radius: 10px;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
  content: '';
  height: 6px;
  left: 10px;
  position: absolute;
  width: 6px;
  top: 10px;
}

.tag::after {
  background: #fff;
  border-bottom: 13px solid transparent;
  border-left: 10px solid #eee;
  border-top: 13px solid transparent;
  content: '';
  position: absolute;
  right: 0;
  top: 0;
}

.tag:hover {
  background-color: crimson;
  color: white;
}

.tag:hover::after {
  border-left-color: crimson;
}

.wiki {
  text-decoration: none;
  color: white;
}
